import React, { useState } from "react";
import "./estimationStyle.scss";
import From from "./EstimationComponents/From";
import To from "./EstimationComponents/To";
import Characters from "./EstimationComponents/Characters";
import Solutions from "./EstimationComponents/Solutions";

const Estimation = () => {
  const [numberOfChars, changeChars] = useState(3);
  const [text, changeCharsOverall] = useState("");
  const [solutionsShown, showSolutions] = useState(0);

  const chars = (chars) => {
    changeChars(chars);
  };
  const charsOverall = (charsOverall) => {
    changeCharsOverall(charsOverall);
  };
  return (
    <section className="estimation">
      <h1 className="estimation__header">
        Profesjonalne tłumaczenia w zasięgu ręki
      </h1>
      <h2 className="estimation__subheader">
        229 743 profesjonalnych tłumaczy i 190 073 klientów korzysta z
        inteligentnego sposobu tłumaczeń od 1999 roku.
      </h2>
      <div className="estimation-main">
        <From />
        <To />
        <Characters charsCallback={chars} charsOverallCallback={charsOverall} />
        <button
          className="estimation__button"
          onClick={() => {
            showSolutions(1);
          }}
        >
          Pokaż ceny
        </button>
      </div>
      {solutionsShown ? (
        <Solutions numberOfChars={numberOfChars} text={text} />
      ) : null}
    </section>
  );
};

export default Estimation;
